let lookArr=["所有人可见","仅自己可见"]
import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import uploadImg from '../../components/common/uploadImg.vue'
import uniPopup from '../../components/uni-popup/uni-popup.vue'
export default {
	components: {
		uniNavBar,
		uploadImg,
		uniPopup
	},
	data() {
		return {
			look:"所有人可见",
			text:"",
			imgList:[],
			showFlag:true,
			isBack:false
		}
	},
	onBackPress() {
		// 如果没有写任何东西就直接返回
		if(!this.text && this.imgList.length<1) {
			return
		}
		//根据返回的提示的操作
		if(!this.isBack) {
			uni.showModal({
				content: '是否要保存为草稿？',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log("保存")
					}else{
						console.log("不保存")
					}
					this.isBack=true;
					uni.navigateBack({
						delta: 1
					});
				},
			});
			return true
		}
		
	},
	methods: {
		// 返回按钮
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		//发布
		submit(){
			console.log("发布");
		},
		//隐私可见
		lookChange(){
			//注意this
			let me = this;
			uni.showActionSheet({
				itemList:lookArr,
				success(res) {
					me.look = lookArr[res.tapIndex];
				}
			})
		},
		upload(arr){
			this.imgList=arr
			console.log(this.imgList);
		},
		//隐藏弹出层
		togglePopup(){
			this.showFlag=false
		}
	}
}
